/**
*  Styles for public table showed in public table
*
*/
html {
  line-height: 1; }

body.public {
  min-width: inherit;
  /* Public table CSS */ }
  body.public .PublicMap-map {
    display: flex;
    overflow: hidden; }
    @media (max-width: 960px) {
      body.public .PublicMap-map {
        flex-direction: column; } }
  body.public .map {
    position: relative;
    padding: 0; }
    body.public .map .cartodb-map {
      position: relative;
      width: 100%;
      height: 100%; }
  body.public .pane_table {
    z-index: 10;
    flex: 1 1 auto;
    overflow: hidden;
    border-radius: 3px;
    text-align: left; }
    body.public .pane_table.is-active {
      background: #FFF; }
  body.public div.panes {
    position: static;
    max-height: 100%;
    margin: 8px;
    clear: both;
    overflow-x: auto;
    overflow-y: hidden; }
    body.public div.panes.pane_map {
      margin-left: 0; }
    @media (max-width: 960px) {
      body.public div.panes {
        display: none;
        margin: 16px; }
        body.public div.panes.is-active {
          display: block; }
        body.public div.panes.pane_map {
          margin-left: 16px; } }
    body.public div.panes div.shadow {
      display: none; }
    body.public div.panes div.table table thead tr th > div > div {
      position: relative; }
  body.public .pane_map {
    position: relative;
    top: 0;
    flex: 1;
    min-width: 35%;
    overflow-x: hidden; }
    @media (max-width: 960px) {
      body.public .pane_map {
        min-width: initial; } }
  body.public .panes .emptyTableContainer {
    position: relative; }
  body.public .dialog {
    position: relative;
    z-index: 10000; }
    body.public .dialog .block.modal.api .content {
      position: relative;
      padding: 0; }
    body.public .dialog .block.modal.api .inner {
      position: relative;
      padding: 28px 40px 0; }
    body.public .dialog .block.modal.api .shadow {
      background-image: -webkit-gradient(linear, 50% 0%, 50% 100%, color-stop(0%, rgba(255, 255, 255, 0.04)), color-stop(100%, rgba(255, 255, 255, 0.95)));
      background-image: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.04) 0%, rgba(255, 255, 255, 0.95) 100%);
      background-image: -moz-linear-gradient(top, rgba(255, 255, 255, 0.04) 0%, rgba(255, 255, 255, 0.95) 100%);
      background-image: -o-linear-gradient(top, rgba(255, 255, 255, 0.04) 0%, rgba(255, 255, 255, 0.95) 100%);
      background-image: linear-gradient(top, rgba(255, 255, 255, 0.04) 0%, rgba(255, 255, 255, 0.95) 100%);
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 60px; }
    body.public .dialog .block.modal.api code {
      color: #666;
      font-size: 13px;
      line-height: 120%; }
    body.public .dialog .block.modal.api .sample {
      padding: 23px 40px;
      border-bottom: 1px solid #E5E5E5;
      background: #F2F2F2; }
      body.public .dialog .block.modal.api .sample p {
        color: #999;
        font-size: 13px; }
    body.public .dialog .block.modal.api h4 {
      margin: 0 0 5px;
      color: #666;
      font-family: "Helvetica Neue", "Helvetica", Arial, sans-serif;
      font-size: 11px;
      text-transform: uppercase; }
    body.public .dialog .block.modal.api .foot {
      padding: 38px; }
      body.public .dialog .block.modal.api .foot p {
        width: 357px;
        padding: 5px 0;
        float: left;
        color: #666;
        font-size: 13px;
        line-height: 120%; }
        body.public .dialog .block.modal.api .foot p a {
          text-decoration: underline; }
  body.public div.separator.bottom {
    display: none;
    position: relative;
    width: 100%;
    border-top: 1px solid #E5E5E5; }
  body.public div.table.public table tbody tr td div.cell {
    cursor: default; }
  body.public div.table.public table tbody tr:hover {
    border-top: 1px solid #ECECEC;
    background: inherit; }
    body.public div.table.public table tbody tr:hover + tr {
      border-top: 1px solid #ECECEC; }
  body.public div.table table thead tr th > div label.interactuable .coloptions {
    line-height: 1; }
    body.public div.table table thead tr th > div label.interactuable .coloptions::after {
      display: none; }
  body.public div.table table thead tr th > div p {
    font-weight: normal; }
  body.public div.table table tfoot.page_loader {
    display: none; }
  body.public .navigation {
    position: relative;
    z-index: 100;
    margin: 8px 0 16px;
    opacity: 0;
    text-align: center; }
    body.public .navigation ul {
      max-width: 1024px;
      margin: auto; }
      body.public .navigation ul {
        *zoom: 1; }
      body.public .navigation ul:before, body.public .navigation ul:after {
        content: "";
        display: table; }
      body.public .navigation ul:after {
        clear: both; }
      body.public .navigation ul li {
        display: inline-block;
        margin: 0 1px 0 0;
        list-style: none; }
        body.public .navigation ul li:last-child {
          margin-left: 28px; }
        body.public .navigation ul li a {
          display: block;
          width: 36px;
          height: 36px;
          border: 1px solid #636D72;
          border-radius: 19px;
          color: #636D72;
          text-align: center; }
          body.public .navigation ul li a i {
            font-size: 14px; }
        body.public .navigation ul li a.selected {
          border-color: #FFF;
          color: #FFF;
          cursor: default; }
  body.public div.table table thead tr th > div p.auto {
    opacity: 0; }
  body.public .cartodb-map-data {
    position: relative;
    z-index: 100;
    top: 300px; }
