/*
 * Map panel for table view
 */
div.map {
  z-index: 1;
  box-sizing: border-box;
  background: #333 url("/assets/1.0.0-assets.136/images/layout/noise.png") repeat 0 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%; }

div.map,
.mobile_bkg {
  padding: 84px 84px 20px 20px; }

div.map.animated .cartodb-map {
  -moz-transition: all 200ms ease;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease; }

div.map {
  /* CartoDB map styles */ }
  div.map.mobile #cartodb-gmaps-attribution {
    display: none; }
  div.map.mobile div.editing {
    z-index: 1000000;
    top: 10px;
    margin-left: -68px; }
    div.map.mobile div.editing span.tooltip {
      left: -68px;
      width: 270px; }
    div.map.mobile div.editing span.tooltip p {
      font-size: 11px; }
  div.map.table div.cartodb-legend-stack {
    position: absolute;
    right: 80px;
    bottom: 20px; }
  div.map.table .leaflet-right .leaflet-control,
  div.map.table #cartodb_attribution {
    margin-right: 62px; }
  div.map.table div.cartodb-map div.cartodb-logo {
    bottom: 0 !important; }
  div.map.table div.cartodb-map {
    position: absolute;
    top: 66px;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 0; }
  div.map div.cartodb-map {
    z-index: 1; }
