/**
 *  Styles for public map showed in (public table or map)
 *
 */
.pane_map .Checkbox {
  position: absolute;
  z-index: 30;
  top: 28px;
  right: 28px;
  padding: 3px 5px;
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.5); }

.pane_map .Checkbox-label {
  color: #FFF;
  font-size: 13px; }

.cartodb-map.public {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  border-radius: 3px; }
  .cartodb-map.public .cartodb-map-wrapper.with_header div.cartodb-zoom,
  .cartodb-map.public .cartodb-map-wrapper.with_header div.cartodb-share,
  .cartodb-map.public .cartodb-map-wrapper.with_header div.cartodb-searchbox,
  .cartodb-map.public .cartodb-map-wrapper.with_header div.cartodb-layer-selector-box {
    margin-top: 70px; }
  .cartodb-map.public div.cartodb-fullscreen {
    position: relative;
    margin: 41px 0 0 20px; }
  .cartodb-map.public div.cartodb-layer-selector-box {
    position: relative;
    margin: 20px 20px 0 0;
    float: right; }
  .cartodb-map.public div.cartodb-share {
    display: none;
    position: relative;
    z-index: 5;
    margin: 20px 20px 0 0;
    float: right; }
  .cartodb-map.public div.cartodb-tiles-loader {
    display: block;
    position: relative;
    top: 0;
    left: 0;
    float: left; }
  .cartodb-map.public div.cartodb-tiles-loader div.loader {
    display: block;
    position: absolute;
    top: 70px;
    left: -30px;
    width: 28px;
    height: 28px;
    margin: 20px 0 0; }
  .cartodb-map.public div.cartodb-zoom {
    position: absolute;
    top: 0;
    margin: 20px 0 0 20px; }
  .cartodb-map.public div.cartodb-tiles-loader {
    position: absolute;
    top: 80px; }
  .cartodb-map.public div.cartodb-searchbox {
    margin-right: 0; }
  .cartodb-map.public .leaflet-right .leaflet-control,
  .cartodb-map.public #cartodb_attribution {
    margin-right: 0 !important; }
