@charset "UTF-8";
/**
 *  Public export styles
 *
 */
.extra_options {
  width: 100%;
  margin: 30px 0 0;
  padding: 13px 0 0;
  float: left;
  clear: both;
  border-top: 1px solid #E5E5E5; }
  .extra_options {
    *zoom: 1; }
  .extra_options:before, .extra_options:after {
    content: "";
    display: table; }
  .extra_options:after {
    clear: both; }
  .extra_options.disabled {
    display: none; }
  .extra_options .row_count {
    display: inline-block;
    color: #858585;
    font-size: 15px;
    vertical-align: top;
    vertical-align: middle; }
  .extra_options .download_options {
    display: inline-block;
    margin: 0 0 0 5px;
    vertical-align: top;
    vertical-align: middle; }
    .extra_options .download_options {
      *zoom: 1; }
    .extra_options .download_options:before, .extra_options .download_options:after {
      content: "";
      display: table; }
    .extra_options .download_options:after {
      clear: both; }
    .extra_options .download_options .download {
      display: inline-block;
      margin: 0 5px 0 3px;
      color: #858585;
      font-size: 15px;
      vertical-align: top;
      vertical-align: middle; }
      .extra_options .download_options .download::after {
        content: '\B7';
        position: relative;
        margin-right: 9px;
        float: left; }
    .extra_options .download_options .loading {
      display: inline-block;
      color: #858585;
      font-size: 15px;
      vertical-align: top;
      vertical-align: middle; }
      .extra_options .download_options .loading img {
        margin: 0 5px 0 0;
        vertical-align: middle; }
    .extra_options .download_options a {
      display: inline-block;
      margin: 0;
      padding: 0 11px;
      font-family: 'Arial';
      font-size: 11px;
      line-height: 27px;
      text-shadow: none;
      text-transform: uppercase;
      vertical-align: top; }
    .extra_options .download_options h2 {
      display: inline-block;
      line-height: normal;
      vertical-align: top; }
  .extra_options .generating {
    display: block; }

.extra_options .user-options {
  float: right; }

.extra_options .button.green.clone {
  display: none;
  position: relative;
  padding: 0 9px 0 28px;
  font-family: 'Arial';
  font-size: 11px;
  letter-spacing: 0;
  line-height: 27px;
  text-shadow: none;
  text-transform: uppercase; }
  .extra_options .button.green.clone i.clone-icon {
    background: url("/assets/1.0.0-assets.136/images/layout/clone-icon.png") no-repeat 0 0;
    position: absolute;
    top: 7px;
    left: 10px;
    width: 14px;
    height: 13px; }

.extra_options .button.grey.edit,
.extra_options .button.grey.oneclick {
  display: none;
  position: relative;
  padding: 0 9px;
  font-family: 'Arial';
  font-size: 11px;
  letter-spacing: 0;
  line-height: 27px;
  text-shadow: none;
  text-transform: uppercase; }

.extra_options .button.grey.oneclick {
  display: inline-block; }

@media only screen and (max-width: 710px) {
  body.public-map .extra_options .download_options,
  body.public .extra_options .download_options {
    display: block !important;
    margin: 10px 0 20px !important; }
    body.public-map .extra_options .download_options .download,
    body.public .extra_options .download_options .download {
      display: none !important; }
  body.public-map .extra_options .user-options,
  body.public .extra_options .user-options {
    width: 100%;
    margin-top: 0;
    float: left !important; } }

@media only screen and (min-width: 711px) and (max-width: 1035px) {
  body.public-map .extra_options .download_options,
  body.public .extra_options .download_options {
    display: block !important;
    margin: 10px 0 20px !important; }
    body.public-map .extra_options .download_options .download,
    body.public .extra_options .download_options .download {
      display: none !important; }
  body.public-map .extra_options .user-options,
  body.public .extra_options .user-options {
    margin-top: 28px; } }
