:-webkit-full-screen {
  width: 100%;
  height: 100%; }
  :-webkit-full-screen #map {
    height: 100% !important; }

:-moz-full-screen {
  width: 100%;
  height: 100%; }
  :-moz-full-screen #map {
    height: 100% !important; }

:-moz-full-screen .cartodb-public-header {
  display: none; }

:-moz-full-screen .separator {
  display: none; }

:-webkit-full-screen .cartodb-public-header {
  display: none; }

:-webkit-full-screen .separator {
  display: none; }

:-ms-fullscreen .cartodb-public-header {
  display: none; }

:-ms-fullscreen .separator {
  display: none; }

:-ms-fullscreen #map {
  height: 100% !important; }

:fullscreen .cartodb-public-header {
  display: none; }

:fullscreen .separator {
  display: none; }
